import React, { useState } from "react";
import logo from "../Img/Atom.png";
import "../Css/header.css";

const Navbar = () => {
  const [Bio, setBio] = useState("Bio");
  const moveBio = () => {
    setBio(window.scroll(0, 0));
  };
  const moveAbout = () => {
    setBio(window.scroll(0, 760));
  };
  const moveSlide = () => {
    setBio(window.scroll(0, 1500));
  };
  const moveCon = () => {
    setBio(window.scroll(0, 2300));
  };

  return (
    <div className="navbar">
      <div className="logo">
        <img src={logo} />
      </div>
      <div className="nav">
        <button onClick={() => moveBio()}>Biography</button>
        <button onClick={() => moveAbout()}>About Me</button>
        <button onClick={() => moveSlide()}>Special Thanks!!</button>
        <button onClick={() => moveCon()}>Contact</button>
      </div>
    </div>
  );
};

export default Navbar;
