import React from "react";
import slide1 from "../Img/tiga.jpg";
import slide2 from "../Img/satu.jpg";
import slide3 from "../Img/dua.jpg";

import bg2 from "../Img/bg2.jpg";

import "../Css/slide.css";

const Slider = () => {
  const sectionStyle = {
    width: "100%",
    height: "730px",
    backgroundImage: `url(${bg2})`
  };
  return (
    <div style={sectionStyle}>
      <div className="allslide">
        <div className="slidertitle">CLICK ME!!!</div>
        <div class="slide">
          <input type="radio" name="radio-set" checked="checked" />
          <img src={slide2} />
          <input type="radio" name="radio-set" />
          <img src={slide3} />
          <input type="radio" name="radio-set" checked="checked" />
          <img src={slide1} />
        </div>
      </div>
    </div>
  );
};

export default Slider;
