import React from "react";
import "../Css/content.css";
import bg1 from "../Img/bg1.jpg";

const Bio = () => {
  return (
    <div className="biographyitems">
      <h1>Anthoni Setiawan</h1>
      <p>
        Lahir pada Tanggal 8 Desember 1997 di Jakarta. Saya sangat ganteng dan
        tak tertandingi serta sangat pintar, tetapi saya tidak sombong dan rajin
        menabung karena menabung pangkal kaya dan saya tak kunjung kaya, tetapi
        saya tidak menyerah untuk menabung. Saya juga parkir di Bedeng yang
        harga parkirannya 5000 karena saya tidak sanggup parkir di Gedung Oleos
        yang Basicly harganya 20 ribu dan liteally kalo di itung pake kalkulator
        20.000 x 20 hari kerja = 400 ribu. Coba di Bedeng 5.000 x 20 hari kerja
        = Cuma 100k cuy. Sekian presentasi saya Terima Kasih.
      </p>
    </div>
  );
};

const BioGraph = () => {
  const sectionStyle = {
    width: "100%",
    height: "800px",
    backgroundImage: `url(${bg1})`
  };
  return (
    <div style={sectionStyle}>
      <div className="bio">
        <div className="biography">
          <Bio />
        </div>
      </div>
    </div>
  );
};
export default BioGraph;
